//--Header-----------------------------------------------------------------------

.header {
    padding: 20px 0;
    position: absolute;
    width: 100%;
    &__icon {
        display: none;
    }
    &__body {
        display: flex;
        position: relative;
        justify-content: space-between;
        align-items: center;
        padding: 18px 0;   
    }
    &__menu {
        ul {
            display: flex;
            padding-left: 0;
            li {
                list-style: none;
                margin-right: 50px;
                a {
                    position: relative;
                    display: inline-block;
                    text-decoration: none;
                    font-family: 'Roboto';
                    font-size: 16px;
                    font-weight: 700;
                    color: #ffffff;
                    
                }
                a:after {
                    content: '';
                    opacity: 0;
                    visibility: hidden;
                    width: 0%;
                    height: 2px;
                    position: absolute;
                    left: 0;
                    bottom: -3px;
                    background:#fff;
                    transition: all 0.5s ease 0s;
                }
                a:hover:after {
                    opacity: 1;
                    visibility: visible;
                    width: 100%;
                }
            }
        }
    }
    &__auth {
        .header__login {
            margin-right: 50px;
        }
        .header__login:after {
            content: '';
            opacity: 0;
            visibility: hidden;
            width: 0%;
            height: 2px;
            position: absolute;
            left: 0;
            bottom: -3px;
            background:#fff;
            transition: all 0.5s ease 0s;
        }
        .header__login:hover:after {
            opacity: 1;
            visibility: visible;
            width: 100%;
        }
        a {
            display: inline-block;
            text-decoration: none;
            font-family: 'Roboto';
            font-size: 16px;
            font-weight: 700;
            color: #ffffff;
            transition: all 0.4s ease 0s;
            position: relative;
        }
    }    
    &__menu a:last-child,
    &__auth a:last-child {
        margin-right: 0;
    }
    &__logo {
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        display: block;
        text-align: center;
        line-height: 1.5;
        width: 54px;
        font-family: 'Ubuntu';
        font-size: 36px;
        font-weight: 700;
        text-transform: uppercase;
        color: #2186eb;
        background: #fff;
        text-decoration: none;
        border-radius: 10px;
    }
    &__sign {
        padding: 16px 62px;
        border: 2px solid #ffffff;
        border-radius: 30px;
    }
    &__sign:hover {
        background: #fff;
        color: #28a3e7;
        opacity: 0.85;
    }
    @media (max-width: 775px) {
        &__menu ul li,
        a.header__login {
            margin-right: 15px;
        }
        &__sign {
            padding: 10px 30px;
        }
    }
}
@media (max-width: 580px) {
    .header__icon {
        display: block;
        position: fixed;
        top: 27px;
        left: 15px;
        z-index: 6;
        width: 30px;
        height: 18px;
        cursor: pointer;
    }
    .header__icon span, .header__icon::before, .header__icon::after {
        position: absolute;
        left: 0;
        height: 10%;
        width: 100%;
        -webkit-transition: all 0.3s ease 0s;
        transition: all 0.3s ease 0s;
        background-color: #fff;
    }
    .header__icon::before {
        content: '';
        top: 0;
    }
    .header__icon::after {
        content: '';
        bottom: 0;
    }
    .header__icon span {
        top: 50%;
        -webkit-transform: scale(1) translate(0px, -50%);
        transform: scale(1) translate(0px, -50%);
    }
    .header__menu {
        position: fixed;
        z-index: 5;
        top: -100%;
        left: 0;
        width: 100%;
        height: 100%;
        background: #28a3e7;
        opacity: 0.95;
        transition: 0.7s ease;
        overflow: auto;
        ul {
            flex-direction: column;
            align-items: center;
            z-index: 5;
            padding-top: 90px;
            padding-bottom: 20px;
            li {
                margin-right: 0;
                margin-bottom: 30px;
                a {
                    font-size: 35px;
                }
            }
        }
    }
    .header__menu::before {
        content: '';
        position: fixed;
        z-index: 3;
        top: 0;
        width: 100%;
        height: 70px;
        background: #28a3e7;
    }
    .header__logo {
        position: fixed;
        z-index: 5;
        left: 85px;
        top: 8px;
        transform: translate(-50%, 0);
    }
    .header__auth {
        position: fixed;
        z-index: 5;
        right: 15px;
        top: 15px;
    }
    .header__icon._active::before {
        top: 50%;
        transform: rotate(-45deg) translate(0px, -50%);
    }
    .header__icon._active::after {
        top: 50%;
        transform: rotate(45deg) translate(0px, 50%);
    }
    .header__icon._active span {
        transform: scale(0);
    }
    .header__menu._active {
        top: 0;
    }
}

//--Intro-----------------------------------------------------------------------

.intro {
    height: 100vh;
    min-height: 770px;
    background-image: url(../img/Hero/02.png);
    background-position: bottom;
    background-repeat: no-repeat;
    padding: 180px 0 55px;
    text-align: center;
    &__title {
        font-size: 66px;
        line-height: 1.2;
        font-weight: 500;
        color: #ffffff;
    }
    &__img {
        display: inline-block;
        margin-top: 60px;
        height: calc(100vh - 60px - 180px - 55px - 158px);
        min-height: 320px;
        img {
            max-height: 100%;
            max-width: 90%;
        }
    }
    @media (max-width: 920px) {
        padding: 150px 20px 50px;
        height: auto;
        min-height: none;
        &__title {
            font-size: 38px;
        }
        &__img {
            margin-top: 50px;
            img {
                width: 210px;
                height: auto;
                max-height: none;
                max-width: none;
            }
        }
    }
}

//--Steps-----------------------------------------------------------------------


.steps {
    margin-top: 38px;
    &__body {
        display: flex;
        justify-content: space-between;
    }
    
    &__item {
        flex: 1 1 33.33%;
        position: relative;
        z-index: 2;
        padding: 165px 45px 45px 45px;
        cursor: pointer;
        @include transit;
        h3 {
            margin-top: 20px;
            font-size: 24px;
            font-weight: 700;
            a {
                color: #2186eb; 
                text-decoration: none; 
            }
            a:hover {
                text-decoration: underline;
            }
        }
        p {
            margin-top: 20px;
            font-size: 16px;
            line-height: 24px;
            font-weight: 400;
            color: #627d98;
        }
    }
    &__item:hover {
        box-shadow: 0px 15px 35px 0px rgba(51,78,104,0.1);
        border-radius: 10px;
    }
    &__icon {
        position: absolute;
        top: 100px;
    }
    &__icon-first {
        top: 89px;
    }
