//--Contact us section------------------------------------------------------
.contact {
    padding: 103px 0 146px;
    background-image: url(../img/Contact/bg.jpg);
    @media (max-width: 920px) {
        padding: 55px 0 30px;
    }
    &__body {
        text-align: center;
    }
    &__text {
        margin-top: 45px;
        font-size: 24px;
        font-weight: 400;
        color: #627d98;
        @media (max-width: 920px) {
            margin-top: 20px;
        }
    }
    form {
        display: inline-block;
        position: relative;
        margin: 95px auto 0;
        @media (max-width: 920px) {
            margin-top: 25px;
        }
        input {
            background-color: #ffffff;
            border-radius: 27.700000000000003px;
            padding: 14px 23px;
            font-size: 16px;
            font-weight: 700;
            color: #2186eb;;
            border: 2px solid #2186eb;
            outline:  none;
            max-width: 275px;
            @media (max-width: 920px) {
                width: 40%;
                max-width: none;
            }
            @media (max-width: 680px) {
                width: 100%;
            }
        }
        select {
            display: inline-block;
            position: relative;
            margin-left: 20px;
            padding: 14px 25px;
            border: 2px solid #2186eb;
            border-radius: 25px;
            width: 275px;
            font-family: Roboto;
            font-size: 16px;
            font-weight: 700;
            color: #2186eb;
            cursor: pointer;
            appearance: none;
            background-image: url(../img/Contact/01.png);
            background-repeat: no-repeat;
            background-position: 242px 22px;
            @media (max-width: 920px) {
                width: 40%;
                max-width: none;
                background-position: 205px 22px;
            }
            @media (max-width: 680px) {
                width: 100%;
                background-position: right 20px top 22px;
                margin-top: 15px;
                margin-left: 0;
            }
        }
        
        select:focus {
            outline: none;
        }
        button {
            width: 175px;
            padding: 14px 0;
            margin-left: 20px;
            background-color: #2186eb;
            border-radius: 25px;
            border: none;
            outline: none;
            font-family: Roboto;
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase;
            color: #ffffff;
            cursor: pointer;
            @include transit;
            @media (max-width: 920px) {
                margin-top: 15px;
                margin-left: 0;
            }
        }
        button:hover {
            background: #28b8ff;
        } 
    }
    form > label {
        display: flex;
        align-items: center;
        cursor: text;
        position: absolute;
        left: 25px;
        top: 25px; 
        transform: translate(0, -50%);
        @include transit;
        @media (max-width: 920px) {
            left: 70px;
        }
        @media (max-width: 680px) {
            left: 25px;
        }
    }
    form > input:focus + label,
    form > input:valid + label {
        transform: scale(0.42);
        top: 0;
        left: 0;
        opacity: 0.7;
        @media (max-width: 920px) {
            left: 50px;
        }
        @media (max-width: 680px) {
            left: 0;
        }
    }
    form > label > span {
        font-family: Roboto;
        font-size: 16px;
        line-height: 18px;
        font-weight: 700;
        color: #2186eb;
        vertical-align: middle;
    }
    &__telegram {
        margin-right: 15px;
        font-size: 24px;
        font-weight: 400;
        color: #2186eb;
    }
    &__req {
        margin-top: 20px;
        @media (max-width: 680px) {
            margin-top: 40px;
        }
        a {
            display: inline-block;
            text-decoration: none;
            font-size: 16px;
            line-height: 24px;
            font-weight: 700;
            color: #2186eb;
            margin-right: 45px;
            @include transit;
            position: relative;
            @media (max-width: 920px) {
                margin-right: 25px;
            }
            @media (max-width: 680px) {
                font-size: 0;
            }
        }
        a:last-child {
            margin-right: 0;
        }
        a:after {
            content: '';
            position: absolute;
            display: inline-block;
            width: 100%;
            height: 1px;
            background-color: #2186eb;
            left: 0;
            bottom: -8px;
            @include transit;
            transform: scale(0);
            @media (max-width: 680px) {
                display: none;
            }
        }
        a:before {
            @media (max-width: 680px) {
                font-size: 32px;
                margin: 0 15px;
                padding: 0;
            }
        }
        a:hover::after {
            opacity: 1;
            visibility: visible;
            transform: scale(1);
        }
    }
    &__email::before {
        content: '\f1fa';
        display: inline-block;
        font-family: 'FontAwesome';
        padding-right: 20px;
        font-size: 24px;
        font-weight: 400;
        color: #2186eb;
        vertical-align: middle;
    }
    &__phone::before {
        content: '\f2a0';
        font-family: 'FontAwesome';
        padding-right: 20px;
        font-size: 24px;
        font-weight: 400;
        color: #2186eb;
        vertical-align: middle;
    }
    &__map::before {
        content: '\f14e';
        font-family: 'FontAwesome';
        padding-right: 20px;
        font-size: 24px;
        font-weight: 400;
        color: #2186eb;
        vertical-align: middle;
    }
    &__mapIcon{
        margin-top: 163px;
        font-size: 100px;
        line-height: 24px;
        font-weight: 400;
        color: #2186eb;
        text-decoration: none;
        @include transit;
        @media (max-width: 920px) {
            margin-top: 0;
            font-size: 0;
        }
    }
    &__mapIcon:hover {
        transform: scale(1.2);
        color: #28b8ff;;
    }
}

